html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: 'DinPro';
  src: url("../fonts/DINPro-Bold.ttf") format("truetype");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}

@font-face {
  font-family: 'Avenir Regular';
  src: url("../fonts/AvenirNextCyr-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Avenir Medium';
  src: url("../fonts/AvenirNextCyr-Medium.ttf") format("truetype");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

body {
  background: #EBEBEE;
  line-height: 1.4;
}

body {
  font-family: 'Avenir Regular', sans-serif;
  font-weight: 400;
  font-size: 24px;
  color: #3C3D40;
  transition: opacity .5s ease;
}

body.loading {
  opacity: 0;
}

a {
  text-decoration: none;
}

.custom {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.custom::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

h1, h2 {
  font-family: 'DinPro', 'Arial', sans-serif;
  font-weight: 700;
  color: #292B33;
}

h1 {
  font-size: 100px;
  text-transform: uppercase;
  line-height: 1em;
  opacity: 0;
}

h1 span {
  opacity: 0;
  transform: translateX(40px);
  display: inline-block;
}

.header {
  height: 100vh;
  position: relative;
}

.header__img {
  width: 1100px;
  height: 689px;
  position: absolute;
  top: 50%;
  left: 55.3%;
  transform: translate(-50%, -50%);
  opacity: 0;
}

.header__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
}

.header__text-content {
  position: absolute;
  top: 60%;
  transform: translateY(-50%);
  z-index: 2;
  mix-blend-mode: exclusion;
  pointer-events: none;
}

.header .descriptor {
  margin-top: 35px;
  line-height: 1em;
  opacity: 0;
  transform: translateX(60px);
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
}

.header__button {
  margin-top: 60px;
  opacity: 0;
  pointer-events: auto;
}

.container {
  padding-left: 6vw;
  padding-right: 6vw;
  width: 100%;
  height: 100%;
}

.button {
  font-size: 18px;
  border: 1px #292B33 solid;
  width: fit-content;
  padding: 14px 50px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: color 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  background: transparent;
}

.button .main-text {
  transition: transform 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  display: inline-block;
}

.button__bg {
  width: 100%;
  height: 100%;
  background: #292B33;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transform: translateY(100%);
  transition: transform 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.button__bg span {
  transform: translateY(100%);
  display: inline-block;
  transition: transform 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-delay: .2s;
}

.button:hover {
  color: white;
}

.button:hover span {
  transform: translateY(-100%);
}

.button:hover .button__bg {
  transform: translateY(0%);
}

.button:hover .button__bg span {
  transform: translateY(0%);
}

.button__white {
  border: 1px white solid;
  color: white;
}

.button__white .button__bg {
  background: white;
}

.button__white:hover {
  color: #292B33;
}

.button__slider {
  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0;
}

.navbar {
  padding-top: 40px;
  width: 100%;
  z-index: 1101;
  position: fixed;
  top: 0;
  left: 0;
}

.navbar__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
}

.logo svg {
  display: block;
}

.logo svg.active {
  fill: white;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1100;
  display: none;
}

.nav__items {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  margin-left: 200px;
}

.nav__item {
  font-size: 40px;
  font-family: 'DinPro', 'Arial', sans-serif;
  font-weight: 700;
  margin-top: 5vh;
}

.nav__item:nth-child(1) {
  margin-top: 0px;
}

.nav__item a {
  text-decoration: none;
  transition: opacity .4s ease;
  color: white;
  opacity: 0.7;
}

.nav__item a:hover {
  opacity: 1;
}

.nav__item a:hover span {
  color: #EC2026;
}

.nav__item a span {
  display: inline-block;
  font-size: 14px;
  margin-right: 25px;
  transition: color .4s ease;
}

.nav__rewealers {
  width: 100%;
  height: 100%;
