}

.grid__item-img-2 {
  position: relative;
  overflow: hidden;
}

.grid__item-img-2 canvas {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.section {
  width: 100%;
  position: relative;
}

.section__container {
  padding-top: 140px;
}

.section__container.last {
  padding-bottom: 140px;
}

.section__container.min-margin {
  padding-top: 100px;
}

.section__container.min-margin.last {
  padding-bottom: 100px;
}

.section p {
  opacity: 0;
}

.section__dark {
  background: #292B33;
  color: white;
}

.section__dark h1, .section__dark h2 {
  color: white;
}

h2 {
  font-size: 60px;
  opacity: 0;
  transform: translateY(40px);
  line-height: 1em;
}

h3 {
  font-size: 24px;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  opacity: 0;
}

.directions {
  margin-top: 80px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.directions__item {
  flex-basis: 32%;
  text-decoration: none;
  color: inherit;
}

.directions__item .img-wrapper {
  margin-top: 15px;
  max-width: 100%;
  opacity: 0;
  transform: translateY(60px);
}

.directions__item h3 {
  width: fit-content;
  overflow: hidden;
}

.directions__item h3:after {
  content: '';
  width: 100%;
  height: 2px;
  display: block;
  background: #292B33;
  transform: translateX(-100%);
