.burger-wrapper {
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1em;
  cursor: pointer;
}

#menu-text.active {
  color: white;
}

.content-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content-wrapper__right {
  margin-left: 8.3vw;
}

.content-wrapper__first {
  margin-top: 80px;
}

.content-wrapper p {
  margin-top: 40px;
}

.content-wrapper .has-margin {
  margin-top: 60px;
}

.content-wrapper__second {
  margin-top: 140px;
}

.footer {
  background: #292B33;
  position: relative;
}

.footer h2 {
  color: white;
}

.footer p {
  color: white;
  margin-top: 50px;
  opacity: 0;
}

.footer a {
  opacity: 0;
  width: fit-content;
}

.footer__container {
  padding-top: 100px;
  padding-bottom: 200px;
  position: relative;
}

.main-footer__content {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.main-footer__content__left-content {
  width: 44%;
}

.main-footer__content__left-content__contacts-items {
  margin-top: 115px;
}

.main-footer__content__left-content__contacts-items a {
  display: block;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 27px;
  transition: color .4s ease;
  font-size: 20px;
}

.main-footer__content__left-content__contacts-items a:hover {
  color: white;
}

.main-footer__content__right-content {
  width: 40%;
  margin-top: 100px;
}

