/*  BURGER
========================================== */
.burger {
  width: 50px;
  height: 50px;
  position: relative;
  border-radius: 4px;
  margin-left: 10px;
}

.burger span {
  position: relative;
  margin-top: 9px;
  margin-bottom: 9px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -15px;
  margin-top: -1.5px;
}

.burger span, .burger span::before, .burger span::after {
  display: block;
  width: 30px;
  height: 3px;
  background-color: #54555C;
  outline: 1px solid transparent;
  -webkit-transition-property: background-color, -webkit-transform;
  -moz-transition-property: background-color, -moz-transform;
  -o-transition-property: background-color, -o-transform;
  transition-property: background-color, transform;
  -webkit-transition-duration: 0.3s;
  -moz-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.burger span::before, .burger span::after {
  position: absolute;
  content: "";
}

.burger span::before {
  top: -9px;
}

.burger span::after {
  top: 9px;
}

.burger.clicked span {
  background-color: transparent;
}

.burger.clicked span::before {
  -webkit-transform: translateY(9px) rotate(45deg);
  -moz-transform: translateY(9px) rotate(45deg);
  -ms-transform: translateY(9px) rotate(45deg);
  -o-transform: translateY(9px) rotate(45deg);
  transform: translateY(9px) rotate(45deg);
}

.burger.clicked span::after {
  -webkit-transform: translateY(-9px) rotate(-45deg);
  -moz-transform: translateY(-9px) rotate(-45deg);
  -ms-transform: translateY(-9px) rotate(-45deg);
  -o-transform: translateY(-9px) rotate(-45deg);
  transform: translateY(-9px) rotate(-45deg);
}

.burger.clicked span:before, .burger.clicked span:after {
  background-color: white;
}

.burger:hover {
  cursor: pointer;
}

.scroll-down {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  height: 60px;
  background: rgba(41, 43, 51, 0.4);
  opacity: 1;
  overflow: hidden;
}

.scroll-down:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: #292B33;
  height: 100%;
  width: 100%;
  animation: scrolling 2.5s infinite cubic-bezier(0.645, 0.045, 0.355, 1);
}

@keyframes scrolling {
  0% {
    transform: translateY(-100%);
  }
  70% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(100%);
  }
}

.icon {
  display: block;
  width: 1.5em;
  height: 2em;
  margin: 0 auto;
  fill: #292B33;
}

.slideshow {
  position: relative;
  width: 100%;
  overflow: hidden;
  margin: 0;
  height: 100vh;
}

.slideshow::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.4s;
}

.slide {
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
  overflow-x: hidden;
  pointer-events: none;
  opacity: 0;
}

.slide--current {
  opacity: 1;
  z-index: 999;
  pointer-events: auto;
  position: relative;
}

.slide__wrap {
  width: 100%;
  overflow: hidden;
  height: 100%;
  position: absolute;
}

.slide__bg,
.slide__img {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: white;
  background-size: cover;
  position: absolute;
  pointer-events: none;
  background-position: 50% 0%;
}

.boxnav {
  position: absolute;
  bottom: 0;
  display: flex;
  z-index: 1000;
  width: 100%;
  left: auto;
  right: 0;
  width: auto;
  background: #EBEBEE;
}

.boxnav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #EBEBEE;
  border: 0;
  left: auto;
  right: 0;
  width: 10vw;
  height: 8vw;
  flex: none;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
}

.boxnav__item:focus {
  outline: none;
}

.boxnav__item--prev:hover,
.boxnav__item--next:hover {
  color: #858585;
}

.boxnav__label {
  padding: 0 0.5rem;
  font-size: 1.15rem;
  position: relative;
}

.boxnav__label--total::before {
  content: '';
  position: absolute;
  right: 100%;
  top: 0;
  width: 2px;
  height: 1.45rem;
  background: #000;
  transform: rotate(22.5deg);
}

.icon--caret-rot {
  transform: rotate(180deg);
}

.form {
  padding: 1px 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
  color: white;
  opacity: 0;
}

.form .button {
  align-self: flex-end;
  margin-top: 60px;
}

.input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}

.input-wrapper.first {
  margin-top: 0px;
}

.input-wrapper input {
  font-size: 16px;
  outline: none;
  border: 0;
  padding-top: 20px;
  padding-bottom: 15px;
  background: transparent;
  color: white;
  font-family: 'Avenir Regular', sans-serif;
  font-weight: 400;
}

.input-wrapper input:focus {
  border: none;
  color: white;
}

.input-wrapper label {
  position: absolute;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1em;
  bottom: 0px;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.input-wrapper label::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 2px solid;
  border-color: white;
  width: 0px;
  transition: width 0.5s ease, border-color .5s ease;
  align-self: center;
  justify-self: center;
}

.label-content {
  position: absolute;
  font-size: 18px;
  font-family: 'DinPro', 'Arial', sans-serif;
  font-weight: 700;
  color: white;
  bottom: 11px;
  left: 0;
  opacity: 0.5;
  transition: transform 0.5s ease, font-size 0.5s ease, opacity 0.5s ease;
}

.focus + .label .label-content {
  transform: translateY(-35px);
  font-size: 14px;
  opacity: 1;
}

.focus + .label:after {
  width: 100%;
}

.form-validate-text {
  color: #F44336;
  display: block;
  margin-top: 5px;
  font-size: 14px;
  position: absolute;
  transition: opacity .3s ease;
  opacity: 0;
  bottom: -27px;
}

.e-label {
  position: absolute;
  right: 6vw;
  font-size: 12px;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.7);
  bottom: 5vh;
  line-height: 1em;
}

.e-label svg {
  width: 18px;
  fill: rgba(255, 255, 255, 0.7);
  transition: fill .3s ease;
}

.e-label span {
  display: inline-block;
  margin-left: 12px;
  margin-top: 2px;
}

.e-label:hover svg {
  fill: #A03ADB;
}
